button:focus {outline:0;}

.navbar-light .navbar-nav .nav-link:hover  {
    color: #ECECEC;
}

.navbar-light .navbar-nav .nav-link.activo {
    color: #ECECEC;
}

code:hover {
    color: #ECECEC;
}

.btn-profile {
    background-color: #8FCAF9;
    color: #424242;
    border: 2px solid #8FCAF9;
    padding: 5px;
    border-radius: 50px;
    width: 200px;
    height: 50px;
    margin: 5px 5px 5px 5px;
    font-weight: bold;
    position: relative;
}

.btn-profile:hover {
    background-color: #424242;
    color: #8FCAF9;
}

.btn-projects {
    background-color: #000000;
    color: #8FCAF9;
    border: 2px solid #000000;
    
    border-radius: 50px;
    width: 100px;
    height: 37px;
    margin: 5px 5px 10px 5px;
}

.btn-projects:hover {
    background-color: #8FCAF9;
    color: #000000;
}

.a-projects {
    color: #ffffff; 
}

.a-projects:hover {
    color: #223179; 
}

.bar {
	fill: #8FCAF9;
}

.axis path,
.axis line {
  fill: none;
  stroke: #ECECEC;
  stroke-width: 1px;
  shape-rendering: crispEdges;
  font-family: 'Darker Grotesque', sans-serif;
}

.toolTip {
  position: absolute;
  display: none;
  min-width: 80px;
  height: auto;
  background: none repeat scroll 0 0 #ECECEC;
  border: 1px solid #8FCAF9;
  padding: 14px;
  text-align: center;
  font-family: 'Darker Grotesque', sans-serif;
}

.axis {
    color: #ECECEC;
}

.contact-icon {
    color: #ECECEC;
}

.contact-icon:hover {
    color: #8FCAF9;
}

#walls {
    margin: 5px auto;
    border: 5px solid #8FCAF9;
}

.snake {
    stroke: #424242;
    fill: #ECECEC;
  }
  .food {
    stroke: #424242;
    fill: #8FCAF9;
  }

  .container {
    font-family: 'Darker Grotesque', sans-serif;
    font-size: 110%;
  }